body,
html,
#root,
.App {
  height: 100%;
}
.lesson_box {
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
}
.lesson_box .text {
  font-size: 14px;
  height: 100%;
  width: 60%;
  overflow: auto;
}
.lesson_box .text header {
  text-align: center;
}
.lesson_box .text section li {
  text-align: left;
}
.lesson_box .text section pre {
  background: #eee;
}
.lesson_box .text section code {
  line-height: 30px;
  padding: 15px;
}
.lesson_box .text section .content {
  font-size: 10px;
}
.lesson_box .canvas {
  height: 100%;
  width: 40%;
  text-align: center;
}
.lesson_box .canvas canvas {
  border: 1px solid;
}


